body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navigation {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  padding: 0 30px;
  color: #163d6f;
}
.menu {
  float: right;
}
.menu ul li {
  float: left;
  font-size: 22px;
  padding: 15px 15px;
  cursor: pointer;
}
.magic {
  float: left;
  width: 30px;
  padding-top: 15px;
  cursor: pointer;
}
.magic p {
  font-size: 10px;
  margin-top: 4px;
}

.fullpage{
    height: 100vh;
    display: flex;
    flex: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }
.first-section{
  color: #163d6f;
  background-color: #FCFDFE;
}
h1.title {
  font-size: 65px;
  font-weight: 700;
}
.first-section h2 {
  font-size: 32px;
  font-weight: 300;
  margin: 30px 0 15px 0;
}
.hashtags {
  margin: 10px 0 30px 0;
  color: #a3b1bc;
}
.social-icon {
  margin: 0 5px;
}

@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}
.down-icon {
  width: 30px;
  font-size: 10px;
  position: absolute;
  bottom: 10px;
  cursor: pointer;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}
.cards-wrapper{
  width: 900px;
  margin: 20px auto 0 auto;
}
.image-wrapper {
  width: 100px;
  margin: 0 auto;
}
.colm-card {
  float: left;
  width: 33.33%;
  padding: 20px;
}
.card {
  background: #fff;
  box-shadow: 3px 3px 15px #d6d6d6;
  border-radius: 8px;
  padding: 40px 20px;
}
.skill-title-wrapper h4 {
  font-size: 18px;
  margin-top: 30px;
}

.top-icon {
    width: 30px;
    font-size: 10px;
    position: absolute;
    bottom: 20px;
    right: 20px;
    cursor: pointer;
  }
.third-section {
  background: #FCFDFE;
  color: #163D6F;
}
.third-section h3 {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 20px;
}

.second-section {
  color: #FCFDFE;
  background-color: #163d6f;
  padding: 0 200px;
}
.second-section h3 {
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 60px;
}
.second-section p {
  font-size: 20px;
  font-weight: 300;
  line-height: 33px;
  text-align: justify;
  margin-bottom: 10px;
}

/**
--------------------------------------------------------------------------------------
-------------- *********************** ResetCss *********************** --------------
--------------------------------------------------------------------------------------
**/
html,body,body div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,figure,footer,header,menu,nav,section,time,mark,audio,video,details,summary{
  margin:0;
  padding:0;
  border:0;
  font-size:100%;
  font-weight:400;
  vertical-align:baseline;
  background:transparent
}
article,aside,figure,footer,header,nav,section,details,summary{
  display:block
}
html{
  box-sizing:border-box
}
*,:before,:after{
  box-sizing:inherit
}
img,object,embed{
  max-width:100%
}
html{
  overflow-y:scroll
}
ul{
  list-style:none
}
blockquote,q{
  quotes:none;
  border: 1px solid #e6ecf5;
  padding: 5px;
  margin-top: 10px;
  border-radius: 5px;
}
blockquote > p{
  margin: 3px 2px !important;
  line-height: 17px !important;
  color: #72799c !important;
}
blockquote > p > a{
  font-weight: 500;
  color: #72799c;
  font-size: 12px;
}
blockquote:before,blockquote:after,q:before,q:after{
  content:'';
  content:none
}
a{
  margin:0;
  padding:0;
  font-size:100%;
  vertical-align:baseline;
  background:transparent
}
del{
  text-decoration:line-through
}
abbr[title],dfn[title]{
  border-bottom:1px dotted #000;
  cursor:help
}
table{
  border-collapse:collapse;
  border-spacing:0
}
th{
  font-weight:700;
  vertical-align:bottom
}
td{
  font-weight:400;
  vertical-align:top
}
hr{
  display:block;
  height:1px;
  border:0;
  border-top:1px solid #ccc;
  margin:1em 0;
  padding:0
}
input,select{
  vertical-align:middle
}
pre{
  white-space:pre;
  white-space:pre-wrap;
  white-space:pre-line;
  word-wrap:break-word
}
input[type="radio"]{
  vertical-align:text-bottom
}
input[type="checkbox"]{
  vertical-align:bottom
}
.ie7 input[type="checkbox"]{
  vertical-align:baseline
}
.ie6 input{
  vertical-align:text-bottom
}
select,input,textarea{
  font:99% sans-serif
}
table{
  font-size:inherit;
  font:100%
}
small{
  font-size:85%
}
strong{
  font-weight:700
}
td,td img{
  vertical-align:top
}
sub,sup{
  font-size:75%;
  line-height:0;
  position:relative
}
sup{
  top:-.5em
}
sub{
  bottom:-.25em
}
pre,code,kbd,samp{
  font-family:monospace,sans-serif
}
.clickable,label,input[type=button],input[type=submit],input[type=file],button{
  cursor:pointer
}
button,input,select,textarea{
  margin:0
}
button,input[type=button]{
  width:auto;
  overflow:visible
}
.ie7 img{
  -ms-interpolation-mode:bicubic
}
.clearfix:before,.clearfix:after{
  content:" ";
  display:block;
  height:0;
  overflow:hidden
}
.clearfix:after{
  clear:both
}
.clearfix{
  zoom:1
}
/**
--------------------------------------------------------------------------------------
-------------- *********************** CustomCss *********************** --------------
--------------------------------------------------------------------------------------
**/
body{font-family: 'Roboto', sans-serif;}
.App {
  text-align: center;
}
::-webkit-scrollbar {
  display: none;
}










